  align-items: center;
  width: 73.5%;
  margin: 0 auto; }

.page-header__logo-link {
  margin-right: 30px; }

.page-header__logo--mobile {
  display: none; }

.page-header__contacts {
  display: flex; }

.page-header__contacts-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none; }

.page-header__contacts-item {
  position: relative; }
  .page-header__contacts-item--mail {
    margin-right: 88px;
    padding-left: 39px; }
    .page-header__contacts-item--mail .page-header__contacts-link::before {
